import { createRouter, createWebHistory } from 'vue-router'
import LoginView from './views/LoginView.vue'
import RegisterView from './views/RegisterView.vue'
import CroissantListView from './views/CroissantListView.vue'
import CroissantDetailsView from './views/CroissantDetailsView.vue'
import ProfileView from './views/ProfileView.vue'

const routes = [
    { path: '/login', component: LoginView },
    { path: '/register', component: RegisterView },
    { path: '/', component: CroissantListView },
    { path: '/croissant/:id', component: CroissantDetailsView },
    { path: '/profile', component: ProfileView }
]

const router = createRouter({
    history: createWebHistory(),
    routes
})

export default router